* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
  margin-left: auto;
  margin-right: auto;
}
.country-input {
  width: 250px;
  outline: none;
}
.country-input:focus {
  border-color: blue;
  border-radius: 3px;
}
.country-item {
  display: flex;
  margin-bottom: 10px;
}
.contry-img {
  margin-right: 10px;
}

.list {
  list-style: none;
}

.country-form {
  padding-left: 8px;
}

.country-list {
  padding-left: 8px;
}

.country-form-name {
  font-weight: 700;
}
